.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
* {
  margin :0;
  padding:0;
  box-sizing: border-box;
}
.body{
 display:flex !important;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 height: 100vh; 
 background: #0d6efd;
}

.login{
  width: 360px;
  height: min-content;
  padding: 20px;
  border-radius: 12px;
  background: #ffffff;
}

.login h1{
  font-size: 36px;
  margin-bottom: 25px;
}

.login form{
  font-size: 20px;
}

.login form .form-group{
  margin-bottom: 12px;

}

.login form input [type="submit"]{
  font-size: 20px;
  margin-top: 15px;
}